import React from "react";
import Table from "./Table";
import Form from "./Form";

class App extends React.Component {
	state = {
		peopleData: []
	};

	handleSubmit = (person) => {
		this.setState({
			// brackets not necessary for single argument
			peopleData: this.state.peopleData.concat([person])
		});
	}

	// function expressions should be used for use of state, otherwise binding
	removePerson = (index) => {
		// get people
		const people = this.state.peopleData;

		// update the state of the component
		this.setState({
			// filter through data
			peopleData: people.filter(function(person, personIndex) {
				// only add people who do not have index of person to be deleted
				return personIndex !== index;
			})
		});
	};

	render() {
		const peopleData = this.state.peopleData;

		return (
			<div className="container">
				<h1>Hello React</h1>
				<Table peopleData={peopleData} removePerson={this.removePerson} />
				<Form handleSubmit={this.handleSubmit} />
			</div>
		);
	};
}

export default App;